.container {
    width: 90%;
    margin: auto;
    text-align: center;
}

.search-bar {
    width: 80%;
    padding: 10px;
    margin: 20px 0;
    font-size: 16px;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

th, td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
}

th {
    background-color: #007BFF;
    color: white;
}

tr:nth-child(even) {
    background-color: #f2f2f2;
}

.add-disaster-form {
    margin-top: 30px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    width: 50%;
    margin: auto;
}

.add-disaster-form h2 {
    margin-bottom: 15px;
}

.add-disaster-form input {
    width: 100%;
    padding: 10px;
    margin: 8px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.add-disaster-form button {
    width: 100%;
    padding: 10px;
    background: #28a745;
    border: none;
    color: white;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 10px;
}

.add-disaster-form button:hover {
    background: #218838;
}
/* Navbar Styling */
/* Navbar Styling */
.navbar {
    background-color: #007bff;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar h2 {
    color: white;
    margin: 0;
}

.navbar ul {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
}

.navbar ul li {
    display: inline;
}

.navbar ul li a {
    color: white;
    text-decoration: none;
    font-weight: bold;
    padding: 10px 15px;
    border-radius: 5px;
    transition: background 0.3s ease;
}

/* Highlight the Login and Register buttons */
.navbar ul li .btn {
    background-color: #28a745;
    padding: 10px 20px;
    border-radius: 5px;
    transition: background 0.3s ease;
}

.navbar ul li .btn:hover {
    background-color: #218838;
}
